import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import cardPage from '../views/cardPage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/cardPage',
    name: 'cardPage',
    component: cardPage
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
